<template>    
    <div>
        <Message :msg="msg" v-show="msg"/>
        <form id="cadastro-form">
            <div class="input-container">
                <label for="date">Data:</label>
                <input v-model="date" type="date" name="date" id="date">
            </div>
            <div class="input-container">
                <label for="codigoEmpresa">Código Empresa:</label>
                <input v-model="codEmpresa" type="text" name="codigoEmpresa" id="codigoEmpresa">
            </div>
            <div class="input-container">
                <label for="nomeEmpresa">Nome Empresa:</label>
                <input v-model="nomeEmpresa" type="text" name="nomeEmpresa" id="nomeEmpresa">
            </div>
            <div class="input-container">
                <label for="valorInicial">Valor Inicial:</label>
                <input v-model.lazy="amount" v-money3="config" v-model="valorInicial" type="text" name="valorInicial" id="valorInicial">
            </div>
            <div class="input-container">
                <label for="valorFinal">Valor Final:</label>
                <input v-model.lazy="amount" v-money3="config" v-model="valorFinal" type="text" name="valorFinal" id="valorFinal">
            </div>
        </form>
        <div class="input-container">
            <button @click="adicionarDados()" class="submit-btn">Cadastrar</button>
        </div>

    </div>

    <div id="form-table">
        <div>
            <div id="form-table-heading">
                <div>Data:</div>
                <div>Cód Empresa:</div>
                <div>Nome Empresa:</div>
                <div>Valor Inicial:</div>
                <div>Valor Final:</div>
            </div>

            <div id="form-table-rows" v-for="dado in dados" :key="dado">
                <div class="form-table-row">                    
                    <div>{{ dado.date }}</div>
                    <div>{{ dado.codEmpresa }}</div>
                    <div>{{ dado.nomeEmpresa }}</div>
                    <div @mousedown="abrirFecharCaixaDeComentario()" id="valor-inicial">{{ dado.valorInicial }}</div>
                    <div v-show="isOpen" v-bind:style="styleObject" class='infowindow-balloon'>
                        <textarea id='comentario'></textarea>
                        <a @click.stop.prevent="abrirFecharCaixaDeComentario()" v-bind:style="styleObjectClose" class='close'>x</a>
                    </div>
                    <div>{{ dado.valorFinal }}</div>
                </div>
                <div class="form-resultado-final">Diferença {{ dado.valorReal }}</div>
            </div>
        </div>
    </div>
    
</template>

<script>
import { Money3Directive } from 'v-money3';
import Message from './Message.vue';

export default {
    name: 'FormCadastro',
    components: {
        Message
    },
    data() {
        return {
            isOpen: true,
            amount: 0,
            dados: [],
            date: '',
            codEmpresa: '',
            nomeEmpresa: '',
            valorInicial: '',
            valorFinal: '',
            valorReal: '',
            valor1: '',
            valor2: '',
            msg: '',
            styleObject: {
                display: 'none',
                fontSize: '16px',
                position: 'absolute',
                marginTop: '-10px',
                marginLeft: '800px',
                padding: '5px',
                color: '#ffffff',
                zIndex: '3000',
                borderRadius: '10px',
                maxWidth: '110px'
            },
            styleObjectClose: {
                position: 'absolute',
                top: '-10px',
                left:' calc(100% - 10px)',
                backgroundColor: '#001040',
                padding: '0 5px',
                borderRadius: '15px',
                cursor: 'pointer !important'
            }
        }
    },
    computed: {
        config() {
            return {
                decimal: ",",
                thousands: ".",
                suffix: "",
                precision: 2,
                masked: false
            }
        },

    },
    directives: {
        money3: Money3Directive

    },
    methods: {
        calcularValores() {
            this.valor1 = parseFloat(this.valorInicial.replace(',', '.'));
            this.valor2 = parseFloat(this.valorFinal.replace(',', '.'));
            this.valorReal = (this.valor2 - this.valor1);
            Number(this.valorReal).toLocaleString('pt-br', {minimumFractionDigits: 10});
            console.log(this.valorReal)
        },
        abrirFecharCaixaDeComentario() {
            this.isOpen = !this.isOpen
        },
        formatarHora() {
            this.date = new Date();
            this.date = new Intl.DateTimeFormat('pt-BR').format(this.date);
        },
        adicionarDados() {
            this.formatarHora();

            if(this.codEmpresa.trim() === '' || this.nomeEmpresa.trim() === '' || 
            this.valorInicial.trim() === '0,00' || this.valorFinal.trim() === '0,00') {
                this.msg = `É obrigatório preencher todos os campos`;
                setTimeout(() => {
                    this.msg = ""                    
                }, 3000);
                return
            } else {
                this.dados.push({
                    date: this.date,
                    codEmpresa: this.codEmpresa,
                    nomeEmpresa: this.nomeEmpresa,
                    valorInicial: this.valorInicial,
                    valorFinal: this.valorFinal,
                    valorReal: this.valorReal
                })
                this.limparCampos();
            }
                                   
        },
        limparCampos() {
            this.date = '',
            this.codEmpresa = '',
            this.nomeEmpresa = '',
            this.valorInicial = '',
            this.valorFinal = ''
        }
    },
    updated() {
        this.calcularValores()
    }
}
</script>

<style scoped>
#cadastro-form {
        max-width: 1200px;
        margin: 0 auto;
    }

    .input-container {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
        margin-bottom: 20px;
        float: left;
        padding-right: 50px;
    }

    label {
        font-weight: bold;
        margin-bottom: 15px;
        color: #222;
        padding: 1px 10px;
        border-left: 4px solid #fc2003;
        height: 15px;
    }

    input #date, #codigoEmpresa, #valorInicial, #valorFinal{
        padding: 5px 10px;
        width: 100px;
        height: 35px;
        margin-bottom: 100px;
    }

    #date {
        width: 110px;
        height: 35px;
    }
    
    #valorInicial, #valorFinal {
        width: 135px;

    }

    #nomeEmpresa {
        padding: 5px 10px;
        width: 300px;
        height: 35px;
    }

    .submit-btn {
        background-color: #001040;
        color: #fff;
        padding: 5px 10px;
        font-size: 16px;
        margin: 0 auto;
        margin-left: 5px;
        margin-top: 30px;
        cursor: pointer;
        transition: .5s;
    }

    .submit-btn:hover {
        background-color: transparent;
        color: #222;
    }

    #form-table {
    max-width: 1200px;
    margin: 0 auto;
    margin-bottom: 50px;
}

#form-table-heading,
#form-table-rows,
.form-table-row {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    padding: 20px 20px;
    margin-bottom: 5px;
}

#form-table-heading {
    font-weight: bold;
    padding: 5px;
    border-bottom: 3px solid #333;
    margin-left: 5px;
    width: 100%;
}

#form-table-heading div,
.form-table-row div {
    width: 20%;
    padding: 5px 50px;
}

#valor-inicial {
    color: #fc2003;
}

.form-table-row {
    padding: 2px;
    border-bottom: 1px solid #ccc;
    margin-left: 1px;
    width: 100%;
}

.form-resultado-final {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    padding: 2px 0 30px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ccc;
    width: 100%;
    
    
}

.search-btn {
    background-color: #222;
    color: #fcba03;
    font-weight: bold;
    border: 2px solid #222;
    padding: 10px;
    font-size: 16px;
    margin: 0 auto;
    cursor: pointer;
    transition: .5s;
    margin-left: 1px;
}


input[type=text],
textarea {
  width: 100px;
}

form.table {
  margin: 20px;
}


</style>